@import "../var/mixins.scss";

.container {
    padding-left: 20px;
    padding-right: 20px;
    margin: 0 auto;

    @include tablet {
        max-width: 768px;
        padding-left: 32px;
        padding-right: 32px;
    }

    @include desktop {
        max-width: 1280px;
        padding-left: 16px;
        padding-right: 16px;
    }
}